@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&family=Roboto&display=swap');
body {
	font-family: 'Roboto', sans-serif;
}
.app {
	padding: 5rem 0 0 0;
	display: flex;
	justify-content: center;
}
.wrap {
	width: 600px;
}
$height: 46px;
.select {
	width: 100%;
	position: relative;
	&.open {
		.select__dropdown {
			display: block;
		}
		.select__input {
			border-bottom: none;
		}
	}
	// .select__input
	&__input {
		border: 1px solid #ccc;
		border-radius: 5px;
		height: $height;
		display: flex;
		align-items: center;
		padding: 0 1rem;
	}
	// .select__dropdown
	&__dropdown {
		display: none;
		border: 1px solid #ccc;
		border-radius: 5px;
		position: absolute;
		top: $height;
		left: 0;
		right: 0;
		max-height: 200px;
		overflow-y: auto;
		box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.3);
	}
}
